@import 'layout-common';
@media @MODE-1 {
  @unitSize    : 80vw/7;               /* @unitSize(7+14/8)=100vw, см ТЗ */


  html {
    font-size  : 2.5vw;                /* Эмпирически 9 символов в @unitSize */

    #app {
      flex-flow  : column;               /* Меню под доской */

      #board {
        width      : 100%;
        height     : 256vw/7;



        .row > * {
          flex-basis : @unitSize;
          height     : @unitSize*6/5;
        }
      }

      #menu {
        width      : @btnCount*100vw/7;
        height     : 100vw/7;
        align-self : center;

        & > * {
          flex-basis : 80vw/7;
          font-size  : 1em/1.25;
          margin     : 10vw/7;
        }
      }
    }
  }
}


