@import 'layout-common';
@media @MODE-2 {
  @unitSize    : 875vh/64;        /* @unitSize(7+14/8+1+2/8)=100vw=100(175vh/128), см ТЗ */
  @maxBrdWidth : 140vh;           /* Почти @unitSize(7+14(1/4)) */

  html {
    font-size  : 3vh;             /* Эмпирически 9 символов в @unitSize */

    #app {
      flex-flow  : row;             /* Меню справа от доски */

      #board {
        max-width  : @maxBrdWidth;
        height     : 175vh/4;
        flex-grow  : 1;
        margin     : 0 auto;

        .row > * {
          flex-basis      : @unitSize;
          height          : @unitSize*6/5;
        }
      }

      #menu {
        width           : @unitSize*5/4;
        flex-flow       : column;


        & > * {
          flex-basis      : @unitSize;
          font-size       : 1em/1.25;
          margin          : @unitSize/8;
        }
      }
    }
  }
}


